import Vue from 'vue'
import App from './App.vue'
import VueParticles from 'vue-particles'
import store from './store/index';
import VueSession from 'vue-session';
Vue.use(VueParticles)
import router from './router'

import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import './assets/gloable.css'


import dataV from '@jiaminghi/data-view'
Vue.use(dataV)

import request from "@/utils/request";
import options from "view-ui-plus/src/components/options";


Vue.prototype.request = request


Vue.use(ElementUI, {size: "small"});

Vue.config.productionTip = false

Vue.use(VueSession);

new Vue({
  router,
  render: h => h(App),
  store,
  VueSession,
}).$mount('#app')

/**
 * 这个flag如果写在beforeEach里就报错，在外面就正常
 * @type {boolean}
 */


router.beforeEach((to, from, next) => {
  // 这个如果放在外面，一开始是空，它就不动了，它是session，不是Vuex+计算属性了，所以就是在放在方法里面，每次都是手动去请求
  const flag = (window.sessionStorage.getItem('userAccount') === null);
  console.log("userAccount: " +window.sessionStorage.getItem('userAccount'));
  console.log("flag: " + flag);

  //路由中设置的needLogin字段就在to当中
  if(!flag){
    console.log("session存在");
    // console.log(to.path) //每次跳转的路径
    if(to.path === '/'){
      //登录状态下 访问login.vue页面 会跳到index.vue
      next({path: '/home'});
    }else{
      next();
    }
  }else{
    console.log("session空白");
    // 如果没有session ,访问任何页面。都会进入到 登录页
    if (to.path === '/login') { // 如果是登录页面的话，直接next() -->解决注销后的循环执行bug
      next();
    } else { // 否则 跳转到登录页面
      next({ path: '/login' });
    }
  }

})
