import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    pendingPackageNumber: '',
    publishedPackageNumber: '',
    removedPackageNumber: '',
    userName:'',
    role:''
  },
  getters: {

  },
  mutations: {
    setPendingPackageNumber(state, newPendingPackageNumber) {
      state.pendingPackageNumber = newPendingPackageNumber;
    },
    setPublishedPackageNumber(state, newPublishedPackageNumber) {
      state.publishedPackageNumber = newPublishedPackageNumber;
    },
    setRemovedPackageNumber(state, newRemovedPackageNumber) {
      state.removedPackageNumber = newRemovedPackageNumber;
    },
    setUserName(state,newUserName) {
      state.userName = newUserName;
    },
    setRole(state, newRole) {
      state.role = newRole;
    }
  },
  actions: {

  },
  modules: {

  }
})
