import Vue from 'vue'
import VueRouter from 'vue-router'
import HomeView from '../views/Manager.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Manage',
    component: () => import('../views/Manager'),
    redirect: "/home",
    meta: {needLogin: true},
    children: [
      //主页路由
      {
        path: 'home',
        name: 'Home',
        component: () => import('../views/Home'),
      },

      //用户中心路由
      {path: 'myResources', name: 'MyResources', component: () => import('../views/MyResources')},
      {path: 'systemLog', name: 'SystemLog', component: () => import('../views/SystemLog')},

      //资源管理路由
      {path: 'uploadNewPackage', name: 'UploadNewPackage', component: () => import('../views/UploadNewPackage')},
      {path: 'pendingReviewPackage', name: 'PendingReviewPackage', component: () => import('../views/PendingReviewPackage')},
      {path: 'publishedPackage', name: 'PublishedPackage', component: () => import('../views/PublishedPackage')},
      {path: 'beRemovedPackage', name: 'BeRemovedPackage', component: () => import('../views/BeRemovedPackage')},


      // 临时上传公共数据的口子
      {path: 'uploadPublicDataTemp', name: 'uploadPublicDataTemp', component: () => import('../views/UploadPublicDataTemp')}

      //工具中心路由
      // {path: 'packingTool', name: 'packingTool', component: () => import('../views/PackingTool')}

    ]
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('../views/Login.vue')
  },
  {
    path: '/404',
    name: "notFound",
    component: () => import('../views/404')
  },
  // 这里应该是路由从上往下匹配，如果没有匹配到，在上述的所用路径之外的任意路径*，都应该转到404页面
  {
    path: '*',
    redirect: '/404'
  }
]

const router = new VueRouter({
  mode: 'history',
  // mode: 'hash',
  base: process.env.BASE_URL,
  routes
})




export default router
